import React, { Component } from 'react';
import {Navbar,NavItem} from 'react-materialize';
import BusStop from './BusStop';
import BusDetail from './BusDetail';
import Ticket from './Ticket';
import {BrowserRouter as  Router, Route, Link} from "react-router-dom";




class Home extends Component {
  render() {
    return (
     <div>
       {/* const routes = (
    <Route path="/" component={Home}>
        <Route path="BusStop"component={BusStop}/>
        <Route path="BusDetail"component={BusDetail}/>
    </Route>
); */}
          <Router>
            <div> 
              <Navbar className="nav">
                <NavItem><Link exact to="./web-app/BusStop">BusStop</Link></NavItem>
                <NavItem><Link to="./web-app/BusDetail">BusDetail</Link></NavItem>
                <NavItem><Link to="./web-app/Ticket">Ticket</Link></NavItem>
              </Navbar>
              <Route path="./web-app/BusStop" component={BusStop}></Route>
              <Route path="./web-app/BusDetail" component={BusDetail}></Route>
              <Route path="./web-app/Ticket" component={Ticket}></Route>
             </div>  
          </Router>   
      </div>
    );
  }
}

export default Home;


